<template>
  <div class="page">
    <ul class="notice">
      <li>
        <svg class="icon icon3" aria-hidden="true">
          <use xlink:href="#icon-keyan"></use>
        </svg>
        <span class="notice_text">科研成果</span>
      </li>
      <li class="line">
        <svg class="icon icon2" aria-hidden="true">
          <use xlink:href="#icon-xiantiao"></use>
        </svg>
      </li>
    </ul>
    <van-dropdown-menu
      active-color="#ee0a24"
      style=" border-top: 1px solid #f2f2f2; border-bottom: 1px solid #f2f2f2;"
    >
      <van-dropdown-item
        v-model="selectSchool"
        :options="schoolList"
        @change="getSubjectList"
      />
      <van-dropdown-item
        v-model="selectYear"
        :options="yearList"
        @change="getSubjectList"
      />
    </van-dropdown-menu>
    <van-row type="flex" justify="space-between">
      <van-col span="6">
        <van-sidebar
          class="navbar"
          v-model="activeKey"
          @change="getSubjectList"
          style="max-height: calc(100% - 8rem);overflow-y: scroll"
        >
          <van-sidebar-item
            v-for="item in menuLists"
            :key="item.id"
            :title="item.catename"
          />
        </van-sidebar>
      </van-col>
      <van-col cols="18" class="artlist">
        <van-pull-refresh v-model="isLoading" @refresh="getSubjectList">
          <van-list
            v-model="loading"
            class="list"
            @load="getSubjectList"
            :finished="finished"
          >
            <van-cell
              is-link
              class="van-ellipsis"
              v-for="(item, index) in articleList"
              :key="index"
              :label="item.time"
              @click="handleToDetail(item.id)"
            >
              <template #title>
                <span class="custom-title">{{ item.entryname }}</span>
              </template>
            </van-cell>
            <van-cell v-if="this.articleList && this.articleList.length == 0">
              <span class="nodata">
                暂时没有文章哦~
              </span>
            </van-cell>
            <van-cell v-if="this.articleList == null">
              <span class="nodata">
                暂时没有文章哦~
              </span>
            </van-cell>
          </van-list>
          <van-pagination
            v-model="currentPage"
            :page-count="pageNum"
            @change="getPageSubjectList"
            mode="simple"
          />
        </van-pull-refresh>
      </van-col>
    </van-row>
  </div>
</template>

<script>
import '../../../public/assets/iconfont'
export default {
  name: 'ResearchList',
  data() {
    return {
      activeKey: 0,
      menuLists: [
        { id: 0, catename: '船舶产业' },
        { id: 1, catename: '金属材料' },
        { id: 2, catename: '能源石化' },
        { id: 3, catename: '汽车配件' },
        { id: 4, catename: '电机电器' },
        { id: 5, catename: '通（专）用设备' },
        { id: 6, catename: '工程机械' },
        { id: 7, catename: '高端装备' },
        { id: 8, catename: '新材料' },
        { id: 9, catename: '生物医药' },
        { id: 10, catename: '电子信息' },
        { id: 11, catename: '节能环保' },
        { id: 12, catename: '其他' }
      ],
      enterprise: '',
      year: '',
      subject: '',
      selectSchool: 0,
      selectYear: 0,
      schoolList: [{ text: '全部高校', value: 0 }],
      yearList: [
        { text: '全部年份', value: 0 },
        { text: '2020', value: 1 },
        { text: '2019', value: 2 },
        { text: '2018', value: 3 },
        { text: '2017', value: 4 },
        { text: '2016', value: 5 },
        { text: '2015', value: 6 },
        { text: '2014', value: 7 },
        { text: '2013', value: 8 },
        { text: '2012', value: 9 },
        { text: '2011', value: 10 },
        { text: '2010', value: 11 },
        { text: '2009', value: 12 },
        { text: '2008', value: 13 },
        { text: '2007', value: 14 },
        { text: '2006', value: 15 },
        { text: '2005', value: 16 }
      ],
      articleList: [],
      loading: false,
      isLoading: false,
      finished: false,
      currentPage: 1,
      pageNum: 1
    }
  },
  methods: {
    async getSelectList() {
      let res = await this.$http.get('/api/home/alluniversity')
      let sList = res.data.data.users
      for (let i = 1; i <= sList.length; i++) {
        let obj = { value: i, text: '' }
        obj.text = sList[i - 1].username
        this.schoolList.push(obj)
      }
    },
    async getSubjectList() {
      this.pageNum = 1
      this.currentPage = 1
      this.subject = this.menuLists[this.activeKey].catename
      if (this.selectSchool !== 0) {
        this.enterprise = this.schoolList[this.selectSchool].text
      } else {
        this.enterprise = ''
      }
      if (this.selectYear !== 0) {
        this.year = this.yearList[this.selectYear].text
      } else {
        this.year = ''
      }
      let res = await this.$http.get(
        '/api/home/selectSci?config=' +
          this.subject +
          '&page=1' +
          '&school=' +
          this.enterprise +
          '&year=' +
          this.year
      )
      if (res.status != 200) return false
      if (res.data.code != 200) {
        this.articleList = res.data.data
        return false
      }
      this.articleList = res.data.data.sci.list
      this.pageNum = res.data.data.sci.pages
      this.loading = false
      this.isLoading = false
      this.finished = true
    },
    async getPageSubjectList() {
      this.subject = this.menuLists[this.activeKey].catename
      if (this.selectSchool !== 0) {
        this.enterprise = this.schoolList[this.selectSchool].text
      } else {
        this.enterprise = ''
      }
      if (this.selectYear !== 0) {
        this.year = this.yearList[this.selectYear].text
      } else {
        this.year = ''
      }
      let res = await this.$http.get(
        '/api/home/selectSci?config=' +
          this.subject +
          '&page=' +
          this.currentPage +
          '&school=' +
          this.enterprise +
          '&year=' +
          this.year
      )
      if (res.status != 200) return false
      if (res.data.code != 200) {
        this.articleList = res.data.data
        return false
      }
      this.articleList = res.data.data.sci.list
      this.pageNum = res.data.data.sci.pages
      this.loading = false
      this.isLoading = false
      this.finished = true
    },
    handleToDetail(id) {
      this.$router.push({
        path: '/researchList/detail/' + id,
        query: {
          subject: this.subject,
          school: this.enterprise,
          year: this.year
        }
      })
    }
  },
  mounted() {
    this.getSelectList()
  }
}
</script>

<style scoped>
.icon1 {
  padding-top: 0.5rem;
  width: 1.25rem;
  height: 1.25rem;
}
.icon2 {
  width: 7.5rem;
  height: 0.5rem;
  fill: #f9ca4f;
}
.icon3 {
  width: 1.4rem;
  height: 1.4rem;
}
.line {
  font-size: 0;
}
.navbar {
  height: 42.875rem;
  overflow-y: scroll;
}
.notice {
  width: 7.5rem;
  margin-left: 0.5rem;
  font-size: 1.25rem;
  margin-top: 0.25rem;
  margin-bottom: 1.25rem;
}
.artlist {
  width: 100%;
}
.nodata {
  color: #757575 !important;
}
.list {
  min-height: 30rem;
}
</style>
